import { AndroidPermissions } from '@ionic-native/android-permissions/ngx';
import { Device } from '@capacitor/device';
import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class DeviceAccess {
  constructor(private androidPermissions: AndroidPermissions) { }
 
  getPhonePermission() {
    this.androidPermissions
      .checkPermission(this.androidPermissions.PERMISSION.READ_PHONE_STATE)
      .then((res) => {
        if (res.hasPermission) {
          console.log('Has Permission Granted');
        } else {
          this.androidPermissions
            .requestPermission(
              this.androidPermissions.PERMISSION.READ_PHONE_STATE
            )
            .then((res) => {
              console.log('Permission Granted', res);
              // alert('Persmission Granted Please Restart App!');
            })
            .catch((error) => {
              console.log('Error in permission', error);
              // alert('Error! ' + error);
            });
          }
        })
        .catch((error) => {
        console.log('Error in permission', error);
        // alert('Error! ' + error);
      });
  }

  async getDeviceId() {
    const info = (await Device.getId()).uuid;
    return info || '';
  };
}