import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class DataService {
  private sendTransactionId = new BehaviorSubject({});
  getTransactionId = this.sendTransactionId.asObservable();

  private sendAccountInfo = new BehaviorSubject({});
  getAccountInfo = this.sendAccountInfo.asObservable();

  private sendAvatarUrl = new BehaviorSubject(null);
  getAvatarUrl = this.sendAvatarUrl.asObservable();

  private sendAppointmentId = new BehaviorSubject({});
  getAppointmentId = this.sendAppointmentId.asObservable();

  public isLoggedIn = new BehaviorSubject<any>(null);    
  public share = this.isLoggedIn.asObservable(); 


  constructor() { }

  shareTransactionId(params: object) {
    this.sendTransactionId.next(params)
  }
  shareAppointmentId(params: object) {
    this.sendAppointmentId.next(params)
  }

  shareAccountInfo(params: object) {
    this.sendAccountInfo.next(params)
  }

  shareAvatarUrl(avatarInfo) {
    this.sendAvatarUrl.next(avatarInfo);
  }

  getCurrentUser() {
    let currentUser = JSON.parse(localStorage.getItem("customer_details"));
    if (currentUser != null) {
      return currentUser;
    } else {
      return null
    }
  }

  getRandomColor(idx:any) {
    var col0 = '#0d856b';
    var col1 = '#d66f1b';
    var col2 = '#9f52e7';
    var col3 = '#e9318d';
    var col4 = '#1175a3';
    var col5 = '#e93131';
    var col6 = '#2316d3';
    var col7 = '#f557f5';
    var col8 = '#d6c31b';
    var col9 = '#40d61b';

    if (idx % 10 == 0) return col0;
    if (idx % 10 == 1) return col1;
    if (idx % 10 == 2) return col2;
    if (idx % 10 == 3) return col3;
    if (idx % 10 == 4) return col4;
    if (idx % 10 == 5) return col5;
    if (idx % 10 == 6) return col6;
    if (idx % 10 == 7) return col7;
    if (idx % 10 == 8) return col8;
    if (idx % 10 == 9) return col9;
    return '#d86315';
  }

}