import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { AutoFocusDirective } from './auto-focus.directive';

const directives = [
  AutoFocusDirective
]

@NgModule({
  declarations: [directives],
  imports: [
    CommonModule
  ],
  exports: directives
})
export class DirectiveModule { }
